import React, {Component} from 'react'
//antd-mobile
import { Icon,Popover } from 'antd-mobile'
//
import {withRouter} from 'react-router-dom'

import './search_header.scss'

const Item = Popover.Item;

class SearchHeader extends Component {
    //
    static defaultProps={
        returnbtn:false,
        value:'',
        pathname:'/'
    }
    //
    constructor(props){
        super(props);
        this.state={
            visible:false,
            selected: '',
        }
    }
    //
    returnPage(){
        this.props.history.push(this.props.pathname||'/')
        sessionStorage.setItem('__search_prev_path__',this.props.pathname||'/')
    }
    //
    onSelect(opt){
        this.setState({
            visible: false,
            selected: opt.props.value,
        });
        //
        this.props.history.push(opt.props.path)
        sessionStorage.setItem('__search_prev_path__',opt.props.path)
    }
    //
    handleVisibleChange(visible){
        this.setState({
            visible,
        });
    }

    render() {
        //
        const myImg = src => <img key={src} src={require(`../../assets/images/${src}.png`)} className="am-icon am-icon-xs" alt="" />;
        //
        let menuArr = [
            (<Item path="/index" value="" icon={myImg('index')}></Item>),
            (<Item path="/cate" value="" icon={myImg('cate')}></Item>),
            (<Item path="/cart" value="" icon={myImg('cart')}></Item>),
            (<Item path="/my" value="" icon={myImg('my')}></Item>),
        ]
        return (
            <div className="search-head">
                {/*  */}
                {
                    this.props.returnbtn
                    ?
                    <div className="left" onClick={()=>{
                        // 
                        this.props.history.go(-1)
                    }}>
                        <img src={require("../../assets/images/return.png")} alt="return"/>
                    </div>
                    :null
                }
                {/*  */}
                <div className="center">
                    {this.props.text}
                </div>
                <div className="right">
                    {/*  */}
                    <Popover mask
                        overlayClassName="fortest"
                        overlayStyle={{ color: 'currentColor' }}
                        visible={this.state.visible}
                        overlay={menuArr}
                        align={{
                            overflow: { adjustY: 0, adjustX: 0 },
                            offset: [-10, 0],
                        }}
                        onVisibleChange={this.handleVisibleChange.bind(this)}
                        onSelect={this.onSelect.bind(this)}
                    >
                        <Icon key="1" onClick={()=>{
                            this.setState({
                                visible:true
                            })
                        }} type="ellipsis" />
                    </Popover>
                </div>
            </div>
        )
    }
}
//
export default withRouter(SearchHeader)